import React, { useState, useEffect } from 'react';
import { Table, Form, Button, Row, Col } from 'react-bootstrap';
import { LinkContainer } from 'react-router-bootstrap';
import { useDispatch, useSelector } from 'react-redux';
import Message from '../../components/eShopComponents/Message';
import Loader from '../../components/eShopComponents/Loader';
import {
 getUserDetails,
 updateUserProfile,
} from '../../actions/eShopActions/userActions';
import { listMyOrders } from '../../actions/eShopActions/orderActions';

const MyOrderScreen = ({ location, history }) => {
 const [name, setName] = useState('');
 const [email, setEmail] = useState('');

 const dispatch = useDispatch();

 const userDetails = useSelector((state) => state.userDetails);
 const { loading, error, user } = userDetails;

 const userLogin = useSelector((state) => state.userLogin);
 const { userInfo } = userLogin;

 const orderListMy = useSelector((state) => state.orderListMy);
 const { loading: loadingOrders, error: errorOrders, orders } = orderListMy;

 useEffect(() => {
  if (!userInfo) {
   history.push('/login');
  } else {
   if (!user || !user.name) {
    dispatch(getUserDetails('profile'));
    dispatch(listMyOrders());
   } else {
    setName(user.name);
    setEmail(user.email);
   }
  }
 }, [dispatch, history, userInfo, user]);

 return (
  <div className="card p-2">
   <Row className={'w-100'}>
    <Col md={12}>
     <h2>My Orders</h2>
     {loadingOrders ? (
      <Loader />
     ) : errorOrders ? (
      <Message variant="danger">{errorOrders}</Message>
     ) : (
      <Table striped bordered hover responsive className="table-sm">
       <thead>
        <tr>
         <th>ID</th>
         <th>DATE</th>
         <th>TOTAL</th>
         <th>PAID</th>
         <th>DELIVERED</th>
         <th>GO TO </th>
        </tr>
       </thead>
       <tbody>
        {orders.map((order) => (
         <tr key={order._id}>
          <td>{order._id}</td>
          <td>{order.createdAt.substring(0, 10)}</td>
          <td>{order.totalPrice}</td>
          <td>
           {order.isPaid ? (
            order.paidAt.substring(0, 10)
           ) : (
            <i className="fas fa-times" style={{ color: 'red' }}></i>
           )}
          </td>
          <td>
           {order.isDelivered ? (
            order.deliveredAt.substring(0, 10)
           ) : (
            <i className="fas fa-times" style={{ color: 'red' }}></i>
           )}
          </td>
          <td>
           <LinkContainer to={`/eshop/order/${order._id}`}>
            <Button className="btn-sm btn-info" variant="light">
             Details
            </Button>
           </LinkContainer>
          </td>
         </tr>
        ))}
       </tbody>
      </Table>
     )}
    </Col>
   </Row>
  </div>
 );
};

export default MyOrderScreen;
